body {
  background-color: whitesmoke;
  font-family: 'Source Sans Pro', sans-serif;
  color: #555555; }

a {
  color: steelblue;
  text-decoration: none; }

a:hover {
  color: cornflowerblue; }

html body a.no-link {
  color: whitesmoke !important; }

html body a.no-link:hover {
  color: whitesmoke !important; }

pre, code, kbd {
  white-space: pre-wrap;
  /* Since CSS 2.1 */
  white-space: -moz-pre-wrap;
  /* Mozilla, since 1999 */
  white-space: -pre-wrap;
  /* Opera 4-6 */
  white-space: -o-pre-wrap;
  /* Opera 7 */
  word-wrap: break-word;
  /* Internet Explorer 5.5+ */ }

ul, form {
  margin-bottom: inherit; }

#footer {
  font-size: 0.6em;
  font-family: 'Source Code Pro', monospace; }

hr {
  margin: 2em 0; }

td {
  border: none; }

/* Utilities */
.smaller {
  font-size: smaller; }

.center {
  text-align: center; }

/* Foreground Colors */
.fg-brown {
  color: #935635; }

.fg-brown2 {
  color: #804000; }

.fg-orange {
  color: #f9b54c; }

.fg-orange2 {
  color: #ffd15d; }

.fg-blue {
  color: steelblue; }

.fg-green {
  color: mediumseagreen; }

.fg-red {
  color: indianred; }

/* Background Colors */
.bg-blue {
  background-color: steelblue;
  color: whitesmoke; }

.bg-green {
  background-color: mediumseagreen;
  color: whitesmoke; }

.bg-red {
  background-color: indianred;
  color: whitesmoke; }

.bg-orange {
  background-color: #f9b54c;
  color: whitesmoke; }

.bg-brown {
  background-color: #935635;
  color: whitesmoke; }

.bg-default {
  background-color: #e8e8e8; }

/* Cards */
.card {
  padding: 1em;
  border-radius: 10px;
  margin-bottom: 1.5em;
  /* Card Customizations */ }
  .card h1, .card h2, .card h3, .card h4, .card h5 {
    margin: 0; }
  .card.bg-blue, .card.bg-green, .card.bg-red, .card.bg-orange, .card.bg-brown {
    padding: 1.5em;
    filter: drop-shadow(5px 5px 5px silver); }
  .card.bg-orange a, .card.bg-brown a {
    color: #ffd15d; }
  .card.bg-red a, .card.bg-blue a, .card.bg-green a {
    color: navajowhite; }
  .card.bg-blue a:hover, .card.bg-red a:hover, .card.bg-green a:hover, .card.bg-orange a:hover, .card.bg-brown a:hover {
    color: #f9b54c; }
  .card p {
    margin: 0; }

/* Header and Navigation */
#header {
  padding-top: 10px;
  padding-bottom: 13px;
  margin-bottom: 2em;
  position: absolute;
  left: 0;
  right: 0;
  top: 0; }
  #header .nav {
    display: inline-block; }
  #header a {
    font-weight: normal;
    color: mediumseagreen;
    vertical-align: top; }
    #header a:hover {
      font-size: larger; }
  #header #menu-toggle {
    line-height: 0.5em;
    display: none;
    color: mediumseagreen; }
  #header #brand {
    display: inline-block;
    font-size: 1.5em;
    font-weight: bold; }
    #header #brand img {
      margin-right: 8px; }
  #header nav {
    float: right;
    position: relative;
    top: 12px; }
  #header ul {
    margin-bottom: 0; }
  #header li {
    list-style: none;
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    min-width: 50px;
    text-align: center;
    border-radius: 5px; }
    #header li i {
      color: mediumseagreen; }
    #header li:last-child {
      margin-right: inherit; }
    #header li:hover, #header li.active {
      background-color: #76c99b;
      background-color: rgba(255, 255, 255, 0.1); }
  #header .navbar-icons li {
    padding: 0; }

.brand {
  font-family: Courier New, monospace;
  font-weight: bold; }

#background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

#main {
  margin-top: 2em; }
  #main h1, #main h2, #main h3, #main h4, #main h5 {
    margin-top: 1em; }
  #main .card {
    filter: drop-shadow(5px 5px 5px #dcdcdc); }
    #main .card.bg-default:nth-child(3n-2) {
      background-color: #dae6f0;
      background-color: RGBA(70, 130, 180, 0.08); }
      #main .card.bg-default:nth-child(3n-2) h4 a {
        color: steelblue; }
    #main .card.bg-default:nth-child(3n-1) {
      background-color: #fcf0db;
      background-color: RGBA(249, 181, 76, 0.08); }
      #main .card.bg-default:nth-child(3n-1) h4 a {
        color: #935635; }
    #main .card.bg-default:nth-child(3n) {
      background-color: #ebf7f0;
      background-color: RGBA(60, 179, 113, 0.08); }
      #main .card.bg-default:nth-child(3n) h4 a {
        color: mediumseagreen; }
  #main code {
    display: block; }
  #main p > code {
    display: inline; }
  #main h2 {
    font-size: 1.8em;
    border-bottom: 1px dashed mediumseagreen;
    padding-left: 12px; }
  #main h3 {
    font-size: 1.55em; }
  #main h4 {
    font-size: 1.2em; }
  #main .item h4 {
    font-size: 1.2em;
    margin-top: 0; }
  #main .item h5 {
    font-size: 1em;
    margin-bottom: 1em; }
  #main .item p {
    font-size: 0.7em; }
  #main .item-tag {
    font-size: 0.7em;
    float: left;
    margin-right: 8px;
    margin-bottom: 8px;
    padding-left: 8px;
    padding-right: 8px;
    border-radius: 5px;
    background-color: #e5e5e5;
    background-color: RGBA(0, 0, 0, 0.1); }
  #main #related > h5 {
    font-size: 1.2em; }
  #main #related .item {
    background: none;
    margin: 0 0 1em 0;
    padding: 0; }
    #main #related .item h4 {
      font-size: 1em; }
    #main #related .item h5 {
      font-size: 0.8em; }
    #main #related .item p {
      float: none;
      margin: 0;
      font-size: 0.7em;
      font-style: italic; }
  #main #related .item-tag {
    display: none; }
  #main .logo {
    width: 2em;
    height: 2em;
    vertical-align: bottom; }
  #main #intro {
    background: url(/image/logo-bg.svg) no-repeat center;
    background-size: 45vh;
    min-width: 45vh;
    min-height: 45vh;
    text-align: center; }
    #main #intro h1 {
      padding-top: 14vh;
      font-size: 3em;
      font-weight: bold;
      color: mediumseagreen;
      margin-bottom: 4px; }
    #main #intro h3 {
      font-size: 1.2em;
      color: mediumseagreen;
      margin-bottom: 2em; }
    #main #intro h2 {
      font-size: 1.5em;
      color: mediumseagreen; }

/* Responsiveness */
@media (max-width: 1130px) {
  #header #menu-toggle {
    display: inline-block;
    position: absolute;
    right: 10px;
    top: 15px; }
  #header #brand {
    display: block; }
  #header nav {
    float: none;
    display: none; }
  #header li {
    text-align: left;
    display: block; }
  #header .navbar-icons {
    display: block; }
    #header .navbar-icons li {
      display: inline-block;
      padding-left: 1em; } }
